body {
  font-family: 'Oswald', sans-serif !important;
}

@media (max-width: 640px) {
  html {
    font-size: 30%;
  }
}
@media (min-width: 640px) {
  html {
    font-size: 60%;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 80%;
  }
}
@media (min-width: 1400px) {
  html {
    font-size: 100%;
  }
}

html,
body,
#root {
  height: 100%;
}
